  viewAttendance(range) {
    // this.startDay = startday.getDay();
    // this.viewDate = startday;
    // var start_date = startday.getTime();
    this.service.getClubAgenda({'rangeTime':range}).subscribe(
      res => {
        console.log(res);
        if (res['result']) {
          this.refresh.next();
          this.events = [];
           console.log(res['result'])
            for (let i=0; i<res['result'].length; i++) {
                var start= new Date(res['result'][i].from);
                var end = new Date(res['result'][i].to);
                var title = `${this.gethoursandminutes(start)} to ${this.gethoursandminutes(end)}
                 member name:${res['result'][i].username}
                trainer name:${res['result'][i].trainername}
                unit name :res['result'][i].unitsname}
                `;
                var color = colors.blue;
                var id =res['result'][i]._id;
                if(res['result'][i].status == "reschedual") color = colors.yellow;
                //if(i%2) var color = colors.blue;else var color = colors.yellow;
                this.events.push({start,end,title,color,id});
            }
        }
      }
    )
  }

